import styles from './styles'

export default function computeStyle (rules, display, data, index) {
  var computed
  var self = this
  for (var i in rules) {
    var rule = rules[i]
    var val = typeof rule === 'function'
      ? rule.call(self, display, data, index, styles.utils)
      : rule
    if (val !== undefined && val !== null) computed = val
  }
  return computed
}
